import {Injectable} from '@angular/core';
import {HttpEvent, HttpHandler, HttpInterceptor, HttpRequest} from '@angular/common/http';
import {Observable} from 'rxjs';
import {CurrentLanguageService} from "../services/current-language.service";
import {first, map, switchMap} from "rxjs/operators";
import {environment} from "../../environments/environment";

@Injectable()
export class LanguageHeaderInterceptor implements HttpInterceptor {

    constructor(
        private readonly currentLanguageService: CurrentLanguageService
    ) {
    }

    intercept(request: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<unknown>> {
        if (request.url.startsWith(environment.api)) {
            return this.currentLanguageService.currentLanguage.pipe(
                first(),
                map(language => {
                    if (language == null) {
                        return request;
                    } else {
                        return request.clone({
                            setHeaders: {
                                'Accept-Language': language
                            }
                        });
                    }
                }),
                switchMap(newRequest => next.handle(newRequest))
            );
        } else {
            return next.handle(request);
        }
    }
}
