import {Injectable} from '@angular/core';
import {AssessmentService, UserAssessmentInput} from "../../generated/api";
import {ResultInput} from "../util/data";
import {Observable} from "rxjs";

@Injectable({
    providedIn: 'root'
})
export class ResultService {

    constructor(
        private readonly assessmentService: AssessmentService
    ) {
    }

    getResults({generalForm, samplesForm, concentrationForm, selectedProductId}: ResultInput): Observable<UserAssessmentInput> {
        if (samplesForm.samples.length !== concentrationForm.entries.length) {
            console.error('samplesForm and concentrationForm mismatch: ', samplesForm, concentrationForm);
            throw new Error('samples.length must be concentrations.length');
        }
        const apiInput: UserAssessmentInput = {
            customer_name: generalForm.customerName ?? undefined,
            customer: generalForm.customerId ?? null,
            country: generalForm.countryId,
            species: generalForm.speciesId,
            test_laboratory: generalForm.testLaboratoryId ?? undefined,
            sample_type: generalForm.sampleTypeId,
            email_recipients: generalForm.recipientEmails?.map(email => ({address: email})) ?? [],
            samples: samplesForm.samples.map((sample, index) => {
                const concentrationEntry = concentrationForm.entries[index];
                return {
                    name: sample.sampleName,
                    inclusion_rate: sample.inclusionRate,
                    component: sample.componentId,
                    contaminations: concentrationEntry.concentrations.map(concentration => {
                        return {
                            mycotoxin: concentration.mycotoxinId,
                            value: concentration.value,
                            level: undefined
                        }
                    }),
                    risk: undefined,
                    product_recommendation: undefined,
                    fcr_factor: undefined,
                    bw_factor: undefined
                };
            }),
            selected_product: selectedProductId,
            moving_risks: undefined,
            total_fcr_factor: undefined,
            total_bw_factor: undefined,
            secret_token: undefined,
            interaction: undefined,
            comments: undefined,
            author: undefined,
            total_risk: undefined,
            total_product_recommendation: undefined,
            created_at: undefined,
            id: undefined
        };
        return this.assessmentService.assessmentCreate(apiInput);
    }

}
