import {AfterViewInit, Component, ElementRef, OnInit, ViewChild} from '@angular/core';

@Component({
    selector: 'app-tutorial-popup',
    templateUrl: './tutorial-popup.component.html',
    styleUrls: ['./tutorial-popup.component.scss']
})
export class TutorialPopupComponent implements OnInit, AfterViewInit {

    @ViewChild('videoElement')
    videoElement: ElementRef;

    constructor() {
    }

    ngOnInit(): void {
    }

    ngAfterViewInit() {
        (this.videoElement.nativeElement as HTMLVideoElement).play().catch(err => {
            console.error('Failed playing video', err);
        });
    }

}
