import { Component, OnDestroy, OnInit } from '@angular/core';
import { DataService } from "./services/data.service";
import { Subject } from "rxjs";
import { debounceTime, takeUntil } from "rxjs/operators";
import { MatDialog } from "@angular/material/dialog";
import { TutorialPopupComponent } from "./modules/components/tutorial-popup/tutorial-popup.component";
import { ActivatedRoute, Router } from "@angular/router";
import { LanguageService } from "./services/language.service";
import { LoginService } from './services/login.service';
import { AccessTokenService } from './services/access-token.service';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, OnDestroy {

    private readonly destroyed$ = new Subject();

    loading: boolean = false;
    loggedIn: string | null = null;
    constructor(
        private readonly dataService: DataService,
        private readonly languageService: LanguageService,
        private readonly matDialog: MatDialog,
        private readonly route: ActivatedRoute,
        private readonly loginService: LoginService,
        private readonly accessTokenService: AccessTokenService,
        private readonly router: Router,
    ) {
    }

    async ngOnInit() {
        this.dataService.loading.pipe(
            debounceTime(200),
            takeUntil(this.destroyed$)
        ).subscribe(loading => {
            this.loading = loading;
        });

        this.loggedIn = this.accessTokenService.currentAccessToken()

        this.route.queryParamMap.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(queryParams => {
            const forcedLanguage = queryParams.get('forceLanguage');
            const user = queryParams.get('user');
            const pass = queryParams.get('pass');
            if (forcedLanguage != null) {
                this.languageService.setLanguage(forcedLanguage);
            }
            if (!this.loggedIn && user && pass) {
                this.signIn(atob(user), atob(pass));
            }
        })


    }
    signIn(email: string, password: string) {
        this.loginService.login(
            email, password
        ).pipe(
            takeUntil(this.destroyed$)
        ).subscribe(loginResult => {
            this.router.navigate([], {
                queryParams: {
                    'user': null,
                    'pass': null,
                },
                queryParamsHandling: 'merge'
            })
        });
    }

    showTutorial() {
        this.matDialog.open(TutorialPopupComponent);
    }

    ngOnDestroy() {
        this.destroyed$.next();
        this.destroyed$.complete();
    }


}
