import { Injectable } from '@angular/core';
import { AccessTokenService } from "./access-token.service";
import {
    catchError,
    distinctUntilChanged,
    finalize,
    first,
    map,
    mapTo,
    share,
    shareReplay,
    switchMap,
    tap
} from "rxjs/operators";
import { ApiService, User, UserService } from "../../generated/api";
import { BehaviorSubject, Observable, of, throwError, timer } from "rxjs";
import { ParsedToken, TokenPair } from "../util/data";
import { ValidAccessTokenHolderService } from "./valid-access-token-holder.service";
import { HttpErrorResponse } from "@angular/common/http";

@Injectable({
    providedIn: 'root'
})
export class LoginService {

    private gettingTokens$ = new BehaviorSubject<boolean>(false);
    readonly currentValidAccessToken: Observable<ParsedToken | null>;
    readonly currentUser: Observable<User | null>;
    readonly loggedIn: Observable<boolean>;

    constructor(
        private readonly accessTokenService: AccessTokenService,
        private readonly validAccessTokenHolderService: ValidAccessTokenHolderService,
        private readonly apiService: ApiService,
        private readonly userService: UserService,
    ) {
        this.currentValidAccessToken = this.accessTokenService.tokens.pipe(
            map(tokens => tokens?.access),
            distinctUntilChanged(),
            switchMap(accessToken => {
                if (accessToken == null) {
                    // console.log('// no token: not logged in, valid token will also be null');
                    // no token: not logged in, valid token will also be null
                    return of(null);
                } else if (accessToken.expires < new Date()) {
                    // console.log('token expired');
                    // if it is expired already, subscription below will refresh it
                    // and we will then get the new one here
                    // so do nothing here yet
                    return of();
                } else {
                    // console.log('// we have a token that we think is valid')
                    // we have a token that we think is valid
                    // check that it actually is valid
                    return this.apiService.apiTokenVerifyCreate({
                        token: accessToken.token
                    }).pipe(
                        mapTo(accessToken),
                        catchError((err) => {
                            console.warn('Token verify failed', err);
                            return of(null);
                        }),
                    );
                }
            }),
            shareReplay(1)
        );

        this.currentValidAccessToken.subscribe(token => this.validAccessTokenHolderService.setAccessToken(token?.token));

        this.currentUser = this.currentValidAccessToken.pipe(
            distinctUntilChanged(),
            switchMap(accessToken => {
                return accessToken == null ? [null] : this.userService.userRetrieve()
            }),
            shareReplay(1)
        );

        this.loggedIn = this.currentUser.pipe(
            map(user => user != null),
            distinctUntilChanged()
        );

        this.accessTokenService.tokens.pipe(
            switchMap(tokens => {
                if (tokens == null) {
                    // console.log("tokens are null");
                    return of<TokenPair<ParsedToken>>();
                } else {
                    return (tokens.access.expires.getTime() <= Date.now() ? of(1) : timer(tokens.access.expires)).pipe(
                        first(),
                        // tap(t => console.log("token expired", t)),
                        mapTo(tokens)
                    );
                }
            }),
            switchMap((tokens) => {
                return this.gettingTokens$.pipe(
                    first(getting => !getting),
                    tap(() => this.gettingTokens$.next(true)),
                    switchMap(() => this.refreshAccessToken(tokens)),
                    tap(newTokens => this.accessTokenService.setTokens(newTokens)),
                    finalize(() => this.gettingTokens$.next(false))
                );
            }),
        ).subscribe();
    }

    get isLoggedIn(): Observable<boolean> {
        return this.currentUser.pipe(
            map(u => u != null)
        );
    }

    login(email: string, password: string): Observable<string | null> {
        return this.gettingTokens$.pipe(
            first(getting => !getting),
            tap(() => this.gettingTokens$.next(true)),
            switchMap(() => {
                return this.apiService.apiTokenCreate({
                    email,
                    password
                } as any)
            }),
            tap((result: any) => { // OpenAPI docs are wrong
                this.accessTokenService.setTokens({
                    access: result.access,
                    refresh: result.refresh
                });
            }),
            mapTo(null),
            catchError(err => {
                if (err instanceof HttpErrorResponse && err.status === 401) {

                    return of(err?.error?.detail || err.message);
                } else {
                    return throwError(err);
                }
            }),
            finalize(() => this.gettingTokens$.next(false)),
            share(),
        );
    }

    logout(): Observable<void> {
        return this.gettingTokens$.pipe(
            first(getting => !getting),
            tap(() => this.accessTokenService.setTokens(null)),
            mapTo(undefined)
        );
    }

    private refreshAccessToken(tokens: TokenPair<ParsedToken>): Observable<TokenPair<string> | null> {
        return this.apiService.apiTokenRefreshCreate({
            refresh: tokens.refresh.token
        } as any).pipe(
            map((apiResponse: any) => { // OpenAPI docs are wrong
                return {
                    access: apiResponse.access,
                    refresh: tokens.refresh.token
                }
            }),
            catchError(err => {
                console.error('Failed refreshing token', err);
                return of(null);
            }),
        );
    }

}
