import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {RegistrationComponent} from "./modules/pages/registration/registration.component";
import {ImprintComponent} from "./modules/pages/imprint/imprint.component";
import {LostPasswordComponent} from "./modules/pages/lost-password/lost-password.component";
import {ResetPasswordComponent} from "./modules/pages/reset-password/reset-password.component";
import {ChangePasswordComponent} from "./modules/pages/change-password/change-password.component";

const routes: Routes = [
    {
        path: 'registration',
        component: RegistrationComponent
    },
    {
        path: 'imprint',
        component: ImprintComponent
    },
    {
        path: 'forgot-password',
        component: LostPasswordComponent
    },
    {
        path: 'reset-password',
        component: ResetPasswordComponent
    },
    {
        path: 'reset-password/:token',
        component: ResetPasswordComponent
    },
    {
      path: 'change-password',
      component: ChangePasswordComponent
    },
    {
        path: 'data',
        loadChildren: () => import('./modules/pages/data-display/data-display.module').then(m => m.DataDisplayModule)
    },
    {
        path: 'moving-risk/:customer_id/:species_id/:for_months',
        loadChildren: () => import('./modules/pages/moving-risk/moving-risk.module').then(m => m.MovingRiskModule)
    },
    {
        path: '',
        loadChildren: () => import('./modules/pages/assessment/assessment.module').then(m => m.AssessmentModule),
    },
];

@NgModule({
    imports: [
        RouterModule.forRoot(routes, {
            initialNavigation: 'enabled',
            relativeLinkResolution: 'corrected',
        }),
    ],
    exports: [RouterModule]
})
export class AppRoutingModule {
}
