import {Injectable} from '@angular/core';
import {BehaviorSubject, fromEvent, Observable, ReplaySubject, Subscription} from "rxjs";

export interface LocalStorageHandle<T> {
    readonly value: Observable<T | null>;
    readonly currentValue: T | null;
    setValue(newValue: T | null);

    subscribe(): Subscription;
}

@Injectable({
    providedIn: 'root'
})
export class LocalStorageService {

    constructor() {

    }

    newHandle<T>(key: string, reader: (raw: string) => T | null, writer: (T) => string): LocalStorageHandle<T> {
        const read = (): T => {
            const raw = localStorage.getItem(key);
            if (raw == null) {
                return null;
            } else {
                return reader(raw);
            }
        }

        const value$ = new BehaviorSubject<T | null>(read());

        return {
            get value(): Observable<T | null> {
                return value$;
            },
            get currentValue(): T | null {
                return value$.getValue();
            },
            setValue(newValue: T | null) {
                value$.next(newValue);
                if (newValue == null) {
                    localStorage.removeItem(key);
                } else {
                    localStorage.setItem(key, writer(newValue));
                }
            },
            subscribe(): Subscription {
                return fromEvent(window, 'storage').subscribe(() => {
                    value$.next(read());
                });
            }
        }
    }

}
