import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { RegisterService } from "../../../../generated/api";
import { first, takeUntil } from "rxjs/operators";
import { forkJoin, Subject } from "rxjs";
import { MatDialog } from "@angular/material/dialog";
import { AlertComponent, AlertData } from "../../components/alert/alert.component";
import { TranslateService } from "@ngx-translate/core";

@Component({
    selector: 'app-registration',
    templateUrl: './registration.component.html',
    styleUrls: ['./registration.component.scss']
})
export class RegistrationComponent implements OnInit, OnDestroy {

    private readonly destroyed$ = new Subject();

    loading = false;
    submitted = false;
    error = false;
    dialog: any;
    controls = {
        name: new FormControl('', Validators.required),
        password: new FormControl('', [Validators.required, Validators.minLength(6)]),
        email: new FormControl('', [Validators.required, Validators.email]),
        country: new FormControl('', Validators.required),
        companyName: new FormControl('', Validators.required),
        companyAddress: new FormControl('', Validators.required),
        privacyAccept: new FormControl(false, Validators.requiredTrue),
        dataSendAccept: new FormControl(false)
    } as const;
    formGroup = new FormGroup(this.controls);

    constructor(
        private readonly registerService: RegisterService,
        private readonly translateService: TranslateService,
        private readonly matDialog: MatDialog
    ) {
    }

    ngOnInit(): void {
    }

    submit() {
        this.submitted = true;

        if (this.formGroup.valid) {
            this.loading = true;
            this.registerService.registerCreate({
                name: this.controls.name.value,
                email: this.controls.email.value,
                country_name: this.controls.country.value,
                company_name: this.controls.companyName.value,
                company_address: this.controls.companyAddress.value,
                password: this.controls.password.value,
                privacy_policy: this.controls.privacyAccept.value,
                nutrition_verification: this.controls.dataSendAccept.value
            }).pipe(
                takeUntil(this.destroyed$),
            ).subscribe({
                next: () => {
                    this.loading = false;
                    const dialog = this.matDialog.open<AlertComponent, AlertData>(AlertComponent, {
                        data: {
                            icon: 'error',
                            iconColor: 'primary',
                            title: 'Thank You',
                            message: 'Your message has been successfully sent. we will contact you very soon',
                            buttons: [
                                {
                                    id: '1',
                                    text: 'OK'
                                }
                            ]
                        },
                        autoFocus: false
                    });


                    dialog.afterClosed().subscribe(result => {
                        if (result?.button === '1') {
                            this.formGroup.reset();
                            this.controls.name.setErrors(null);
                            this.controls.email.setErrors(null);
                            this.controls.country.setErrors(null);
                            this.controls.companyName.setErrors(null);
                            this.controls.password.setErrors(null);
                            this.controls.privacyAccept.setErrors(null);
                            this.controls.dataSendAccept.setErrors(null);
                            this.controls.companyAddress.setErrors(null);
                            this.formGroup.markAsUntouched();
                            this.formGroup.markAsPristine();
                        }
                    });
                },
                error: err => {
                    this.loading = false;
                    console.error('Registration failed', err);
                    this.dialog = this.matDialog.open<AlertComponent, AlertData>(AlertComponent, {
                        data: {
                            icon: 'error',
                            iconColor: 'warn',
                            title: 'Registration Failed',
                            message: err?.error?.email ? err?.error?.email?.join(',') : 'Something Went wrong, please check with adminstrator',
                            buttons: [
                                {
                                    id: '2',
                                    text: 'OK'
                                }
                            ]
                        },
                        autoFocus: false
                    });
                }
            });
        }
    }

    ngOnDestroy() {
        this.destroyed$.next();
        this.destroyed$.complete();
    }

}
