import {Component, EventEmitter, OnDestroy, OnInit, Output} from '@angular/core';
import {FormControl, FormGroup} from "@angular/forms";
import {Subject} from "rxjs";
import {distinctUntilChanged, takeUntil} from "rxjs/operators";
import {KnownLanguage, LanguageService} from "../../../services/language.service";
import {MatDialog} from "@angular/material/dialog";
import {SignInComponent} from "../sign-in/sign-in.component";
import {LoginService} from "../../../services/login.service";
import {User} from "../../../../generated/api";
import {LayoutService} from "../../../services/layout.service";
import {FormDataService} from "../../../services/form-data.service";
import {Router} from "@angular/router";
import {MovingRiskPopupComponent} from "../moving-risk-popup/moving-risk-popup.component";

@Component({
    selector: 'app-navigation-bar',
    templateUrl: './navigation-bar.component.html',
    styleUrls: ['./navigation-bar.component.scss']
})
export class NavigationBarComponent implements OnInit, OnDestroy {

    private readonly destroyed$ = new Subject();

    @Output()
    showTutorial = new EventEmitter<void>();

    currentUser: User | null = null;

    knownLanguages: ReadonlyArray<KnownLanguage> = [];
    controls = {
        language: new FormControl(null)
    } as const;
    formGroup = new FormGroup(this.controls);

    desktopView: boolean = false;

    constructor(
        private readonly formDataService: FormDataService,
        private readonly layoutService: LayoutService,
        private readonly languageService: LanguageService,
        private readonly loginService: LoginService,
        private readonly matDialog: MatDialog,
        private readonly router: Router
    ) {
    }

    ngOnInit(): void {
        this.languageService.currentLanguage.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(language => {
            this.controls.language.setValue(language, {emitEvent: false});
        });
        this.controls.language.valueChanges.pipe(
            distinctUntilChanged(),
            takeUntil(this.destroyed$)
        ).subscribe(newLanguage => {
            this.languageService.setLanguage(newLanguage);
        });

        this.languageService.knownLanguages.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(knownLanguages => {
            this.knownLanguages = knownLanguages;
        });

        this.loginService.currentUser.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(currentUser => {
            this.currentUser = currentUser;
        });

        this.layoutService.stepperVerticalMode.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(verticalMode => this.desktopView = !verticalMode);
    }

    ngOnDestroy() {
        this.destroyed$.next();
        this.destroyed$.complete();
    }

    signIn() {
        this.matDialog.open(SignInComponent, {
            autoFocus: false
        });
    }

    reset() {
        this.formDataService.goToRoot();
    }

    logout() {
        this.loginService.logout().subscribe();
    }

    openMovingRisk() {
        this.matDialog.open(MovingRiskPopupComponent, {
            autoFocus: false,
        });
    }

}
