import {Component, OnDestroy, OnInit} from '@angular/core';
import {FormControl, FormGroup, FormGroupDirective, NgForm, Validators} from "@angular/forms";
import {Observable, of, Subject} from "rxjs";
import {takeUntil} from "rxjs/operators";
import {TranslateService} from "@ngx-translate/core";
import {ApiService} from "../../../../generated/api";
import {MatDialog} from "@angular/material/dialog";
import {ErrorStateMatcher} from "@angular/material/core";
import {HttpErrorResponse} from "@angular/common/http";

@Component({
    selector: 'app-lost-password',
    templateUrl: './lost-password.component.html',
    styleUrls: ['./lost-password.component.scss']
})
export class LostPasswordComponent implements OnInit, OnDestroy {

    private readonly destroyed$ = new Subject();

    loading = false;
    submitted = false;
    error: Observable<string> | null = null;

    controls = {
        email: new FormControl(null, [Validators.required, Validators.email])
    } as const;
    formGroup = new FormGroup(this.controls);

    emailErrorMatcher: ErrorStateMatcher;

    constructor(
        private readonly matDialog: MatDialog,
        private readonly apiService: ApiService,
        private readonly translateService: TranslateService
    ) {
        const component = this;
        this.emailErrorMatcher = new class extends ErrorStateMatcher {
            isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
                return super.isErrorState(control, form) || component.error != null;
            }
        }
    }

    ngOnInit(): void {
    }

    submit() {
        if (this.formGroup.valid) {
            this.loading = true;
            this.error = null;
            this.apiService.apiPasswordResetCreate({
                email: this.controls.email.value
            }).pipe(
                takeUntil(this.destroyed$),
            ).subscribe({
                next: () => {
                    this.loading = false;
                    this.submitted = true;
                },
                error: err => {
                    this.loading = false;
                    console.error('Lost Password failed', err);
                    if (err instanceof HttpErrorResponse && Math.floor(err.status / 100) === 4 && typeof err.error === 'object' && Array.isArray(err.error.password)) {
                        this.error = of(err.error.password.map(p => String(p)).join(', '));
                    } else {
                        this.error = this.translateService.get('LOSTPASSWORD_FAILED');
                    }
                }
            });
        }
    }

    ngOnDestroy() {
        this.destroyed$.next();
        this.destroyed$.complete();
    }

}
