import {Injectable} from '@angular/core';
import {BehaviorSubject, Observable} from "rxjs";
import {isRawTokenPair, ParsedToken, TokenPair} from "../util/data";
import {LocalStorageHandle, LocalStorageService} from "./local-storage.service";
import jwtDecode from "jwt-decode";

@Injectable({
    providedIn: 'root'
})
export class AccessTokenService {

    private readonly username$ = new BehaviorSubject<string | null>(null);
    private readonly storedToken: LocalStorageHandle<TokenPair<ParsedToken>>;

    constructor(
        private readonly localStorageService: LocalStorageService,
    ) {
        this.storedToken = this.localStorageService.newHandle<TokenPair<ParsedToken>>(
            'risk.token',
            raw => {
                const jwts = AccessTokenService.readToken(raw);
                if (jwts == null) {
                    return null;
                } else {
                   return AccessTokenService.parseJwts(jwts);
                }
            },
            (token) => JSON.stringify({refresh: token.refresh.token, access: token.access.token})
        );
        this.storedToken.subscribe();
    }

    currentAccessToken(): string | null {
        return this.storedToken.currentValue?.access?.token;
    }

    get tokens(): Observable<TokenPair<ParsedToken> | null> {
        return this.storedToken.value;
    }

    setTokens(pair: TokenPair<string> | null) {
        this.storedToken.setValue(pair == null ? null : AccessTokenService.parseJwts(pair));
    }

    private static readToken(raw: string): TokenPair<string> | null {
        try {
            const parsed = JSON.parse(raw);
            return isRawTokenPair(parsed) ? parsed : null;
        } catch (e) {
            console.error('Failed parsing token from local storage', e);
            return null;
        }
    }

    private static parseJwts(pair: TokenPair<string>): TokenPair<ParsedToken> | null {
        const access = AccessTokenService.parseJwt(pair.access);
        const refresh = AccessTokenService.parseJwt(pair.refresh);
        return access == null || refresh == null ? null : {access, refresh};
    }

    private static parseJwt(token: string): ParsedToken | null {
        try {
            const decoded = jwtDecode(token);
            const exp = decoded['exp'];
            if (typeof exp !== 'number') {
                console.error('JWT has no valid exp', exp);
                return null;
            }
            const expires = new Date(exp * 1000);
            const rawUserId = decoded['user_id'];
            const userId = rawUserId == null || (typeof rawUserId !== 'string' && typeof rawUserId !== 'number') ? null : rawUserId;
            return {
                expires,
                token,
                userId
            };
        } catch (e) {
            console.error('Failed decoding JWT', e);
            return null;
        }
    }


}
