import { Injectable } from '@angular/core';
import { AbstractControl, FormArray, FormControl, FormGroup, Validators } from "@angular/forms";
import { LoginService } from "./login.service";
import { ApiData, ApiDataIdx, DataService, makeIndex, ProcessedProduct } from "./data.service";
import {
    catchError,
    defaultIfEmpty,
    distinctUntilChanged,
    filter,
    first,
    map,
    shareReplay,
    startWith,
    switchMap,
    tap,
    throttleTime
} from "rxjs/operators";
import {
    Components,
    Country,
    Customer,
    Level,
    SampleTypes,
    Species,
    User,
    UserAssessmentInput,
    UserAssessmentMovingRisk
} from "../../generated/api";
import { BehaviorSubject, combineLatest, forkJoin, Observable, of, ReplaySubject, Subject } from "rxjs";
import { ResultService } from "./result.service";
import { TranslateService } from "@ngx-translate/core";
import { MovingRiskData, ResultInput, ResultOutput } from "../util/data";
import { LanguageService } from "./language.service";
import { AlertComponent, AlertData, AlertResult } from "../modules/components/alert/alert.component";
import { MatDialog } from "@angular/material/dialog";
import { Router } from "@angular/router";

export interface ConcentrationUnit {
    readonly text: string;
    readonly toPPB: (input: number) => number;
}

export interface SampleFormInfo {
    sampleName: string | null;
    component: Components | null;
    inclusionRate: any;
}

export const concentrationUnits: ReadonlyArray<ConcentrationUnit> = [
    {
        text: 'ppb (g/kg)',
        toPPB: input => input
    },
    {
        text: 'ppm (mg/kg)',
        toPPB: input => input * 1000
    }
];

@Injectable({
    providedIn: 'root'
})
export class FormDataService {

    readonly speciesNewFormControls = {
        speciesNew: new FormControl(null, Validators.required)
    } as const;
    readonly speciesNewForm = new FormGroup(this.speciesNewFormControls);

    readonly generalFormControls = {
        customerName: new FormControl(null),
        country: new FormControl(null, Validators.required),
        species: new FormControl(null, Validators.required),
        laboratory: new FormControl(null),
        sampleType: new FormControl(null, Validators.required),
        recipients: new FormArray([FormDataService.createRecipientControl()])
    } as const;
    readonly generalForm = new FormGroup(this.generalFormControls);

    readonly samplesFormArray: FormArray = new FormArray([FormDataService.createSamplesRow(true)]);
    readonly samplesFormControls = {
        formArray: this.samplesFormArray
    } as const;
    readonly samplesForm = new FormGroup(this.samplesFormControls);

    readonly concentrationForms = new FormArray([FormDataService.createConcentrationForm()]);
    readonly completeConcentrationForm = new FormGroup({
        entries: this.concentrationForms
    });

    readonly productSelectionControls = {
        selectedProduct: new FormControl(null, Validators.required)
    } as const;
    readonly productSelectionForm = new FormGroup(this.productSelectionControls);

    readonly allControls = {
        generalForm: this.generalForm,
        speciesNewForm: this.speciesNewForm,
        samplesForm: this.samplesForm,
        concentrationForm: this.completeConcentrationForm,
        productSelectionForm: this.productSelectionForm
    } as const;
    readonly completeForm = new FormGroup(this.allControls);

    readonly sampleFormInfo: Observable<SampleFormInfo[]>;

    readonly sampleType: Observable<SampleTypes | null>;
    readonly species: Observable<Species | null>;
    readonly country: Observable<Country | null>;

    readonly processedProducts: Observable<ProcessedProduct[]>;
    readonly validComponents: Observable<Components[] | null>;
    readonly validComponentsIdx: Observable<Map<string, Components> | null>;

    readonly apiOutput: Observable<UserAssessmentInput | null>;
    readonly resultOutput: Observable<ResultOutput | null>;

    readonly onResultStep: Observable<boolean>;
    readonly onProductSelectStep: Observable<boolean>;

    private readonly reset$ = new Subject<void>();
    private readonly currentStep$ = new BehaviorSubject<number>(0);
    private data: ApiData | null = null;
    private loggedIn = false;
    private currentUser: User | null = null;
    private currentSampleType: SampleTypes | null = null;
    private readonly apiErrors$ = new ReplaySubject<unknown | null>(1);

    constructor(
        private readonly matDialog: MatDialog,
        private readonly router: Router,
        private readonly dataService: DataService,
        private readonly resultService: ResultService,
        private readonly loginService: LoginService,
        private readonly languageService: LanguageService,
        private readonly translateService: TranslateService,
    ) {
        this.sampleType = this.observeIdSelection(this.generalFormControls.sampleType, d => d.sampleTypes);
        this.species = this.observeIdSelection(this.generalFormControls.species, d => d.species);
        this.country = this.observeIdSelection(this.generalFormControls.country, d => d.countries);
        this.validComponents = combineLatest([this.sampleType, this.species]).pipe(
            switchMap(([sampleType, species]) => {
                if (sampleType == null || species == null) {
                    return [null];
                } else {
                    return this.dataService.getComponents(sampleType.id, species.id);
                }
            }),
            shareReplay(1)
        );
        this.validComponentsIdx = this.validComponents.pipe(
            map(components => components == null ? null : makeIndex(components)),
            shareReplay(1)
        );

        this.onResultStep = combineLatest([this.currentStep$, this.loginService.loggedIn]).pipe(
            map(([currentStep, loggedIn]) => currentStep === (loggedIn ? 4 : 3))
        );

        this.onProductSelectStep = combineLatest([this.currentStep$, this.loginService.loggedIn]).pipe(
            map(([currentStep, loggedIn]) => loggedIn && currentStep === 3)
        );

        this.processedProducts = combineLatest([this.country, this.species]).pipe(
            switchMap(([country, species]) => {
                if (country == null || species == null) {
                    return of<ProcessedProduct[]>([]);
                } else {
                    return this.dataService.get('products').pipe(
                        map(products => {
                            return this.dataService.processProducts(country.id, products).filter(product => {
                                // OpenAPI generator is broken
                                return (species.has_products as unknown as (string[] | undefined))?.includes(product.id)
                            })
                        })
                    );
                }
            })
        );

        this.sampleFormInfo = combineLatest([this.samplesFormArray.valueChanges, this.validComponents]).pipe(
            map<[unknown, Components[] | null], SampleFormInfo[]>(([, components]) => {

                return this.samplesFormArray.controls.map(sampleForm => {
                    return {
                        component: components?.find(c => c.id === sampleForm.get('component').value),
                        sampleName: sampleForm.get('name').value ?? null,
                        inclusionRate: components?.find(c => c.id === sampleForm.get('inclusionRate').value)
                    }
                });
            }),
            shareReplay(1)
        );

        this.sampleType.subscribe(sampleType => {
            this.currentSampleType = sampleType ?? null
            this.updateInclusionRateState();
        });

        this.loginService.currentUser.subscribe(currentUser => {
            this.loggedIn = currentUser != null;
            this.currentUser = currentUser;
            this.updateLoginState();
        });

        this.dataService.data.subscribe(data => {
            this.data = data;
            for (const concentrationForm of this.concentrationForms.controls as FormGroup[]) {
                this.updateMycotoxinRows(concentrationForm);
            }
        });

        this.productSelectionForm.valueChanges.pipe(
            startWith(0),
            switchMap(() => {
                return this.productSelectionControls.selectedProduct.value == null ? [null] : this.loginService.loggedIn.pipe(
                    map(loggedIn => loggedIn ? 4 : 3)
                )
            }),
            distinctUntilChanged(),
            filter(step => step != null)
        ).subscribe(step => this.setCurrentStep(step));

        this.apiOutput = combineLatest([this.completeForm.valueChanges, this.completeForm.statusChanges, this.currentStep]).pipe(
            startWith(0),
            switchMap(() => this.dataService.data),
            switchMap(() => {
                if (this.completeForm.invalid || this.currentStep$.getValue() !== (this.loggedIn ? 4 : 3)) {
                    return [null];
                } else {
                    return this.getResultInput();
                }
            }),
            throttleTime(500, undefined, { leading: false, trailing: true }),
            switchMap(resultInput => {
                if (resultInput == null) {
                    this.apiErrors$.next(null);
                    return [null];
                } else {
                    return this.resultService.getResults(resultInput).pipe(
                        tap(() => this.apiErrors$.next(null)),
                        catchError(err => {
                            console.warn('Failed getting API result', err);
                            this.apiErrors$.next(err);
                            return of(null);
                        })
                    );
                }
            }),
            distinctUntilChanged((a, b) => (a == null) === (b == null)),
            shareReplay(1)
        );

        this.resultOutput = this.apiOutput.pipe(
            switchMap(apiOutput => {
                return this.makeResultOutput(apiOutput);
            })
        );
    }

    get apiErrors(): Observable<unknown | null> {
        return this.apiErrors$;
    }

    makeMovingRiskData(apiMovingRisks: ReadonlyArray<UserAssessmentMovingRisk> | null, allLevels: Level[]): ReadonlyArray<MovingRiskData> {
        return apiMovingRisks?.map(risk => {
            const avgRiskFactorNumber = parseFloat(risk.avg_risk_factor);
            const roundedLevelFactor = Math.round(avgRiskFactorNumber);
            const matchingLevel = allLevels.find(l => l.level_factor === roundedLevelFactor);
            return {
                count: risk.number_of_assessments,
                date: new Date(Date.parse(risk.month)),
                level: matchingLevel,
                rawLevel: avgRiskFactorNumber
            }
        });
    }

    makeResultOutput(apiOutput: UserAssessmentInput | null): Observable<ResultOutput | null> {
        if (apiOutput == null) {
            return of(null);
        } else {
            const date = apiOutput.created_at == null ? null : new Date(Date.parse(apiOutput.created_at));
            return combineLatest([
                this.dataService.getIdx('species').pipe(map(species => species.get(apiOutput.species))),
                this.dataService.getIdx('sampleTypes').pipe(map(sampleTypes => sampleTypes.get(apiOutput.sample_type))),
                this.languageService.currentLanguage,
                this.dataService.get('products').pipe(
                    map(products => this.dataService.processProducts(apiOutput.country, products))
                ),
                this.dataService.getIdx('levels'),
                this.dataService.getComponentsIdx(apiOutput.sample_type, apiOutput.species),
                this.dataService.getIdx('countries').pipe(map(countries => countries.get(apiOutput.country)))
            ]).pipe<ResultOutput>(
                map(([species, sampleType, currentLanguage, processedProducts, levels, components, country]: [Species, SampleTypes, string, ProcessedProduct[], Map<string, Level>, Map<string, Components>, Country]) => {
                    const allLevels = Array.from(levels.values());
                    const movingRiskData: ReadonlyArray<MovingRiskData> = this.makeMovingRiskData(apiOutput.moving_risks, allLevels);
                    return {
                        apiData: apiOutput,
                        movingRiskData,
                        species,
                        sampleType,
                        dateString: date?.toLocaleDateString(currentLanguage, {
                            year: "numeric",
                            month: "2-digit",
                            day: "2-digit"
                        }),
                        country,
                        totalBwFactor: apiOutput.total_bw_factor == null ? null : parseFloat(apiOutput.total_bw_factor),
                        totalFcrFactor: apiOutput.total_fcr_factor == null ? null : parseFloat(apiOutput.total_fcr_factor),
                        samples: apiOutput.samples.map(sample => {
                            return {
                                apiData: sample,
                                recommendation: sample.product_recommendation == null ? null : {
                                    product: sample.product_recommendation.product == null ? null : processedProducts.find(p => p.id === sample.product_recommendation?.product) ?? null,
                                    text: sample.product_recommendation?.text ?? null
                                },
                                sampleRisk: sample.risk == null ? null : this.data.levels.find(lvl => lvl.id === sample.risk) ?? null,
                                component: components.get(sample.component) ?? null,
                                inclusionRateDisplay: sampleType.has_inclusion_rate ? sample.inclusion_rate?.toLocaleString(currentLanguage) ?? '--' : null,
                                bwFactor: sample.bw_factor == null ? null : parseFloat(sample.bw_factor),
                                fcrFactor: sample.fcr_factor == null ? null : parseFloat(sample.fcr_factor)
                            }
                        }),
                        mycotoxinRows: this.data.mycotoxins.map(mycotoxin => {
                            return {
                                mycotoxin,
                                values: apiOutput.samples.map(sampleEntry => {
                                    const contaminationEntry = sampleEntry.contaminations.find(entry => entry.mycotoxin === mycotoxin.id);
                                    const display = contaminationEntry?.value?.toLocaleString(currentLanguage) ?? 'nt';
                                    return {
                                        display,
                                        level: contaminationEntry?.level == null ? null : this.data.levels.find(lvl => lvl.id === contaminationEntry.level) ?? null
                                    }
                                })
                            }
                        }),
                        totalRecommendedProduct: apiOutput.total_product_recommendation == null ? null : {
                            product: processedProducts.find(p => p.id === apiOutput.total_product_recommendation.product) ?? null,
                            text: apiOutput.total_product_recommendation.text
                        },
                        totalRiskLevel: apiOutput.total_risk == null ? null : levels.get(apiOutput.total_risk) ?? null
                    };
                })
            );
        }
    }

    get reset(): Observable<void> {
        return this.reset$;
    }

    get currentStep(): Observable<number> {
        return this.currentStep$;
    }

    setCurrentStep(index: number) {
        this.currentStep$.next(index);
    }

    goToRoot() {
        if (this.router.url === '/') {
            if (this.generalForm.dirty) {
                const dialog = this.matDialog.open<AlertComponent, AlertData, AlertResult>(
                    AlertComponent,
                    {
                        autoFocus: false,
                        data: {
                            title: 'RESET_DATA_HEADER',
                            message: 'RESET_DATA_MESSAGE',
                            buttons: [
                                {
                                    id: 'yes',
                                    text: 'GENERAL_YES',
                                    role: 'primary'
                                },
                                {
                                    id: 'no',
                                    text: 'GENERAL_NO'
                                }
                            ]
                        }
                    }
                );
                dialog.afterClosed().subscribe(result => {
                    if (result?.button === 'yes') {
                        this.resetAll();
                    }
                });
            } else {
                this.resetAll();
                return of(undefined);
            }
        } else {
            this.router.navigate(['/']);
        }
    }

    resetAll() {
        this.completeForm.reset();
        this.samplesFormArray.clear();
        this.samplesFormArray.push(FormDataService.createSamplesRow(true));
        this.concentrationForms.clear();
        const concentrationForm = FormDataService.createConcentrationForm();
        this.concentrationForms.push(concentrationForm);
        this.generalFormControls.recipients.clear();
        this.generalFormControls.recipients.push(FormDataService.createRecipientControl());
        this.updateLoginState();
        this.updateInclusionRateState();
        this.updateMycotoxinRows(concentrationForm)

        this.reset$.next();
        this.setCurrentStep(0);
        // need to do this twice, because the 2nd time we potentially autofill the email
        this.updateLoginState();
    }

    addRecipient() {
        this.generalFormControls.recipients.push(FormDataService.createRecipientControl());
        this.updateLoginState();
    }

    removeRecipient(index: number) {
        this.generalFormControls.recipients.removeAt(index);
    }

    addSample() {
        this.samplesFormArray.push(FormDataService.createSamplesRow());
        const newConcentrationForm = FormDataService.createConcentrationForm();
        this.concentrationForms.push(newConcentrationForm);
        this.updateMycotoxinRows(newConcentrationForm);
        this.updateInclusionRateState();
    }

    removeSample(index: number) {
        this.samplesFormArray.removeAt(index);
        this.concentrationForms.removeAt(index);
    }

    private getResultInput(): Observable<ResultInput> {
        const sampleNames$: Observable<string>[] = this.samplesFormArray.controls.map((sampleForm, sampleIndex) => {
            const givenName = sampleForm.get('name').value;
            if (givenName == null || /^\s*$/.test(givenName)) {
                return this.translateService.get('STEPTWO_SAMPLENAME_DEFAULT', { index: sampleIndex + 1 }).pipe(first());
            } else {
                return of(givenName);
            }
        });

        return forkJoin(sampleNames$).pipe(
            defaultIfEmpty([]),
            map((sampleNames) => {
                return {
                    generalForm: {
                        customerName: typeof this.generalFormControls.customerName.value === 'string' ? this.generalFormControls.customerName.value : ((this.generalFormControls.customerName.value as Customer | null)?.name ?? ''),
                        customerId: typeof this.generalFormControls.customerName.value === 'string' ? null : (this.generalFormControls.customerName.value as Customer | null)?.id,
                        countryId: this.generalFormControls.country.value,
                        speciesId: this.generalFormControls.species.value,
                        testLaboratoryId: this.generalFormControls.laboratory.value,
                        sampleTypeId: this.generalFormControls.sampleType.value,
                        recipientEmails: this.loggedIn ? this.generalFormControls.recipients.controls.map(c => c.value) : []
                    },
                    samplesForm: {
                        samples: this.samplesFormArray.controls.map((samplesForm, samplesIdx) => {
                            const inclusionRateValue = samplesForm.get('inclusionRate').value;
                            let parsedInclusionRate = null;
                            if (inclusionRateValue == null) {
                                parsedInclusionRate = null;
                            } else if (typeof inclusionRateValue === 'string') {
                                parsedInclusionRate = parseInt(inclusionRateValue);
                            } else if (typeof inclusionRateValue === 'number') {
                                parsedInclusionRate = inclusionRateValue
                            }
                            if (parsedInclusionRate != null && (!isFinite(parsedInclusionRate) || parsedInclusionRate < 0)) {
                                parsedInclusionRate = null;
                            }
                            return {
                                sampleName: sampleNames[samplesIdx],
                                componentId: samplesForm.get('component').value,
                                inclusionRate: parsedInclusionRate
                            }
                        })
                    },
                    concentrationForm: {
                        entries: this.concentrationForms.controls.map(concentrationForm => {
                            const concentrationEntries = concentrationForm.get('concentrations') as FormArray;
                            if (this.data == null || concentrationEntries.length !== this.data.mycotoxins.length) {
                                return {
                                    concentrations: []
                                }
                            } else {
                                return {
                                    concentrations: concentrationEntries.controls.map((concentrationRowForm, mycotoxinIndex) => {
                                        const value = (concentrationRowForm.get('unit').value as ConcentrationUnit).toPPB(
                                            concentrationRowForm.get('value').value
                                        );
                                        return {
                                            mycotoxinId: this.data.mycotoxins[mycotoxinIndex].id,
                                            value: value
                                        }
                                    })
                                };
                            }
                        })
                    },
                    selectedProductId: this.productSelectionControls.selectedProduct.value?.productId
                }
            })
        );
    }

    private observeIdSelection<V>(control: FormControl, getter: (data: ApiDataIdx) => Map<string, V>): Observable<V> {
        return control.valueChanges.pipe(
            startWith(control.value as string | null),
            switchMap((currentId: string | null) => {
                if (currentId == null) {
                    return of(null);
                } else {
                    return this.dataService.dataIdx.pipe(
                        map(idx => getter(idx).get(currentId) ?? null)
                    );
                }
            }),
            shareReplay(1)
        );
    }

    private updateMycotoxinRows(concentrationForm: FormGroup) {
        const concentrations = concentrationForm.get('concentrations') as FormArray;
        if (this.data == null) {
            concentrations.clear();
        } else {
            if (concentrations.length !== this.data.mycotoxins.length) {
                concentrations.clear();
                for (const mycotoxin of this.data.mycotoxins) {
                    concentrations.push(FormDataService.createConcentrationRow());
                }
            }
        }
    }

    private updateInclusionRateState() {
        const showInclusionRate = this.currentSampleType?.has_inclusion_rate ?? false;
        for (const control of this.inclusionRateOnlyFields()) {
            if (showInclusionRate) {
                control.enable();
            } else {
                control.disable();
            }
        }
    }

    private * inclusionRateOnlyFields(): IterableIterator<AbstractControl> {
        for (const row of this.samplesFormArray.controls) {
            yield row.get('inclusionRate');
        }
    }

    private updateLoginState() {
        for (const ctrl of this.loginOnlyFields()) {
            if (this.loggedIn) {
                ctrl.enable();
            } else {
                ctrl.disable();
            }
        }
        // console.log("updateLoginState", this.currentUser, this.generalFormControls.recipients.at(0).value);
        if (this.currentUser != null && this.generalFormControls.recipients.length === 1 && !this.generalFormControls.recipients.at(0).value) {
            // console.log("updating state")
            this.generalFormControls.recipients.at(0).setValue(this.currentUser.email);
        }
    }

    private * loginOnlyFields(): IterableIterator<AbstractControl> {
        yield* [this.generalFormControls.customerName, this.generalFormControls.laboratory, this.generalFormControls.recipients];
        // yield* this.generalFormControls.recipients.controls;
        yield this.generalFormControls.recipients;
        yield this.productSelectionForm;
    }

    private static createRecipientControl(): FormControl {
        return new FormControl(null, []);
    }

    private static createSamplesRow(i = false): FormGroup {
        let group: any;
        // if (i) {
        group = {
            component: new FormControl(null, Validators.required),
            name: new FormControl(null, Validators.required),
            inclusionRate: new FormControl(null, Validators.required)
        };
        // } else {
        //     group = {
        //         component: new FormControl(null),
        //         name: new FormControl(null),
        //         inclusionRate: new FormControl(null, [
        //             Validators.min(0), Validators.max(100),
        //             Validators.pattern(/^\d*$/)
        //         ])
        //     };
        // }
        return new FormGroup(group)

    }

    private static createConcentrationForm(): FormGroup {
        return new FormGroup({
            concentrations: new FormArray([])
        });
    }

    private static createConcentrationRow(): FormGroup {
        return new FormGroup({
            value: new FormControl(null, [
                Validators.min(0), Validators.pattern(/^\d*$/)
            ]),
            unit: new FormControl(concentrationUnits[0])
        });
    }

}
