import { NgModule, ModuleWithProviders, SkipSelf, Optional } from '@angular/core';
import { Configuration } from './configuration';
import { HttpClient } from '@angular/common/http';

import { AssessmentService } from './api/assessment.service';
import { BackendService } from './api/backend.service';
import { ComponentsService } from './api/components.service';
import { CountriesService } from './api/countries.service';
import { CustomersService } from './api/customers.service';
import { DatabaseService } from './api/database.service';
import { LaboratoriesService } from './api/laboratories.service';
import { LanguagesService } from './api/languages.service';
import { LevelsService } from './api/levels.service';
import { MycotoxinsService } from './api/mycotoxins.service';
import { ProductsService } from './api/products.service';
import { RegisterService } from './api/register.service';
import { SampleTypesService } from './api/sampleTypes.service';
import { SendReportService } from './api/sendReport.service';
import { SpeciesService } from './api/species.service';
import { TranslationsService } from './api/translations.service';
import { UserService } from './api/user.service';

@NgModule({
  imports:      [],
  declarations: [],
  exports:      [],
  providers: []
})
export class ApiModule {
    public static forRoot(configurationFactory: () => Configuration): ModuleWithProviders<ApiModule> {
        return {
            ngModule: ApiModule,
            providers: [ { provide: Configuration, useFactory: configurationFactory } ]
        };
    }

    constructor( @Optional() @SkipSelf() parentModule: ApiModule,
                 @Optional() http: HttpClient) {
        if (parentModule) {
            throw new Error('ApiModule is already loaded. Import in your base AppModule only.');
        }
        if (!http) {
            throw new Error('You need to import the HttpClientModule in your AppModule! \n' +
            'See also https://github.com/angular/angular/issues/20575');
        }
    }
}
