import {Components, Country, Level, Mycotoxins, SampleTypes, Species, UserAssessmentInput, UserAssessmentSample} from "../../generated/api";
import {ProcessedProduct} from "../services/data.service";
import {unzip} from "zlib";

export interface NamedSample {
    readonly componentId: string;
    readonly sampleName: string;
    readonly inclusionRate: number | null;
}

export interface GeneralFormData {
    readonly customerName: string | null;
    readonly customerId: number | null;
    readonly countryId: string;
    readonly speciesId: string;
    readonly testLaboratoryId: string | null;
    readonly sampleTypeId: string;
    readonly recipientEmails: string[] | null;
}

export interface SamplesFormData {
    readonly samples: ReadonlyArray<NamedSample>;
}

export interface ConcentrationEntry {
    readonly mycotoxinId: string;
    readonly value: number;
}

export interface SingleConcentrationFormData {
    readonly concentrations: ReadonlyArray<ConcentrationEntry>;
}

export interface ConcentrationFormData {
    readonly entries: ReadonlyArray<SingleConcentrationFormData>;
}

export interface TokenPair<T> {
    readonly refresh: T;
    readonly access: T;
}

export function isRawTokenPair(input: unknown): input is TokenPair<string> {
    return typeof input === 'object' && typeof input['refresh'] === 'string' && typeof input['access'] === 'string';
}

export interface ParsedToken {
    readonly token: string;
    readonly expires: Date;
    readonly userId: string | number | null;
}

export interface ResultInput {
    readonly generalForm: GeneralFormData;
    readonly samplesForm: SamplesFormData;
    readonly concentrationForm: ConcentrationFormData;
    readonly selectedProductId: string | null;
}

export interface ProcessedProductRecommendation {
    product: ProcessedProduct | null;
    text: string | null;
}

export interface MycotoxinResultValue {
    display: string;
    level: Level | null;
}

export interface MycotoxinResultRow {
    mycotoxin: Mycotoxins;
    values: Array<MycotoxinResultValue>;
}

export interface ResultSampleData {
    readonly apiData: UserAssessmentSample;
    recommendation: ProcessedProductRecommendation | null;
    sampleRisk: Level | null;
    component: Components | null;
    inclusionRateDisplay: string | null;
    readonly bwFactor: number | null;
    readonly fcrFactor: number | null;
}

export interface ResultOutput {
    readonly apiData: UserAssessmentInput;
    readonly species: Species;
    readonly sampleType: SampleTypes;
    readonly country: Country;
    readonly totalRiskLevel: Level | null;
    readonly totalRecommendedProduct: ProcessedProductRecommendation | null;
    readonly dateString: string;
    readonly mycotoxinRows: MycotoxinResultRow[];
    readonly samples: ResultSampleData[];
    readonly totalBwFactor: number | null;
    readonly totalFcrFactor: number | null;
    readonly movingRiskData: ReadonlyArray<MovingRiskData> | null;
}

export interface MovingRiskData {
    readonly level: Level;
    readonly rawLevel: number;
    readonly count: number;
    readonly date: Date;
}

export interface ApiResultSuccess {
    readonly type: 'success';
    readonly response: UserAssessmentInput;
}

export interface ApiResultError {
    readonly type: 'error';
    readonly error: unknown;
}
