import {Injectable} from '@angular/core';
import {
    Components,
    ComponentsService,
    CountriesService,
    Country,
    LaboratoriesService,
    Laboratory,
    Level,
    LevelsService,
    Mycotoxins,
    MycotoxinsService,
    Products,
    ProductsService,
    SampleTypes,
    SampleTypesService,
    Species,
    SpeciesService
} from '../../generated/api';
import {BehaviorSubject, combineLatest, forkJoin, Observable, of} from 'rxjs';
import {catchError, distinctUntilChanged, filter, finalize, first, map, mapTo, shareReplay, single, skipWhile, startWith, switchMap, tap, throttleTime} from 'rxjs/operators';
import {LanguageService} from './language.service';
import {LoginService} from './login.service';

export interface ApiData {
    readonly countries: Country[];
    readonly species: Species[];
    readonly laboratories: Laboratory[];
    readonly sampleTypes: SampleTypes[];
    readonly mycotoxins: Mycotoxins[];
    readonly products: Products[];
    readonly levels: Level[];
    readonly components: Components[];
}

export type HasId = {
    id: string;
}

export interface ApiDataIdx {
    readonly sampleTypes: Map<string, SampleTypes>;
    readonly species: Map<string, Species>;
    readonly countries: Map<string, Country>;
    readonly levels: Map<string, Level>;
    readonly products: Map<string, Products>;
    readonly components: Map<string, Components>;
}

export interface ProcessedProduct {
    id: string;
    name: string;
    logo?: string | null;
}

type LoadState = 'initial' | 'loading' | 'loaded' | 'error';

interface ComponentsCache {
    sampleTypeId: string;
    speciesId: string;
    languageId: string;
    components: Components[];
}

export function makeIndex<T extends HasId>(input: T[]): Map<string, T> {
    return new Map(input.map(value => [value.id, value]))
}

@Injectable({
    providedIn: 'root'
})
export class DataService {

    private readonly state$ = new BehaviorSubject<LoadState>('initial');
    private readonly data$ = new BehaviorSubject<ApiData | null>(null);
    private readonly componentsLoading$ = new BehaviorSubject<boolean>(false);
    private readonly lastComponents$ = new BehaviorSubject<ComponentsCache | null>(null);
    private readonly dataIdx$: Observable<ApiDataIdx | null>;

    loading = this.state$.pipe(
        map(state => state === 'loading' || state === 'initial'),
        distinctUntilChanged()
    );

    constructor(
        private readonly countryService: CountriesService,
        private readonly speciesService: SpeciesService,
        private readonly laboratoriesService: LaboratoriesService,
        private readonly sampleTypesService: SampleTypesService,
        private readonly componentsService: ComponentsService,
        private readonly mycotoxinsService: MycotoxinsService,
        private readonly productsService: ProductsService,
        private readonly levelsService: LevelsService,
        private readonly loginService: LoginService,
        private readonly languageService: LanguageService,
    ) {
        this.dataIdx$ = this.data$.pipe(
            map<ApiData | null, ApiDataIdx | null>(apiData => {
                if (apiData == null) return null;
                return {
                    sampleTypes: makeIndex(apiData.sampleTypes),
                    species: makeIndex(apiData.species),
                    countries: makeIndex(apiData.countries),
                    levels: makeIndex(apiData.levels),
                    products: makeIndex(apiData.products),
                    components: makeIndex(apiData.components),
                }
            }),
            shareReplay(1)
        );

        const languageChange = this.languageService.currentLanguage.pipe(
            distinctUntilChanged()
        );
        const tokenChange = this.loginService.currentValidAccessToken.pipe(
            distinctUntilChanged((a, b) => a?.userId === b?.userId)
        );
        combineLatest([languageChange, tokenChange]).pipe(
            throttleTime(400, undefined, {leading: true, trailing: true}),
            switchMap(() => this.reload()),
        ).subscribe();
    }

    get data(): Observable<ApiData> {
        return this.data$.pipe(filter(d => d != null));
    }

    get dataIdx(): Observable<ApiDataIdx> {
        return this.dataIdx$.pipe(filter(d => d != null));
    }

    get<T extends keyof ApiData>(key: T): Observable<ApiData[T]> {
        return this.data.pipe(
            map(data => data[key])
        );
    }

    getIdx<T extends keyof ApiDataIdx>(key: T): Observable<ApiDataIdx[T]> {
        return this.dataIdx.pipe(
            map(data => data[key])
        );
    }

    getComponents(sampleTypeId: string, speciesId: string): Observable<Components[]> {
        return this.doGetComponents(sampleTypeId, speciesId).pipe(shareReplay(1));
    }

    getComponentsIdx(sampleTypeId: string, speciesId: string): Observable<Map<string, Components>> {
        return this.doGetComponents(sampleTypeId, speciesId).pipe(
            map(components => makeIndex(components)),
            shareReplay(1)
        );
    }

    reload(): Observable<void> {
        return this.awaitNotLoading().pipe(
            switchMap(() => this.doReload())
        );
    }

    processProducts(countryId: string, products: Products[]): ProcessedProduct[] {
        return products.map(product => {
            const countryRule = product.product_country_rules.find(rule => rule.country === countryId);
            if (countryRule == null) {
                return product;
            } else {
                return {
                    id: product.id,
                    ...countryRule
                };
            }
        })
    }

    private doGetComponents(sampleTypeId: string, speciesId: string): Observable<Components[]> {
        return this.languageService.currentLanguage.pipe(
            switchMap(languageId => this.getComponentsOnce(languageId, sampleTypeId, speciesId)),
        );
    }

    private getComponentsOnce(languageId: string, sampleTypeId: string, speciesId: string): Observable<Components[]> {
        return this.awaitComponentsNotLoading().pipe(
            tap(() => this.componentsLoading$.next(true)),
            switchMap(() => this.maybeGetComponentsFromCache(languageId, sampleTypeId, speciesId)),
            finalize(() => this.componentsLoading$.next(false)),
        );
    }

    private maybeGetComponentsFromCache(languageId: string, sampleTypeId: string, speciesId: string): Observable<Components[]> {
        const current = this.lastComponents$.getValue();
        if (current == null || current.sampleTypeId !== sampleTypeId || current.speciesId !== speciesId || current.languageId !== languageId) {
            return this.componentsService.componentsList2(sampleTypeId, speciesId).pipe(
                tap(components => this.lastComponents$.next({languageId, sampleTypeId, speciesId, components})),
                single()
            );
        } else {
            return of(current.components)
        }
    }

    private awaitComponentsNotLoading(): Observable<void> {
        return this.componentsLoading$.pipe(
            skipWhile(loading => loading),
            first(),
            mapTo(undefined)
        );
    }

    private awaitNotLoading(): Observable<void> {
        return this.state$.pipe(
            skipWhile(state => state === 'loading'),
            first(),
            mapTo(undefined)
        );
    }

    private doReload(): Observable<void> {
        return forkJoin({
            countries: this.countryService.countriesList(),
            species: this.speciesService.speciesList(),
            laboratories: this.laboratoriesService.laboratoriesList(),
            sampleTypes: this.sampleTypesService.sampleTypesList(),
            mycotoxins: this.mycotoxinsService.mycotoxinsList(),
            products: this.productsService.productsList(),
            levels: this.levelsService.levelsList(),
            components: this.componentsService.componentsList(),
        }).pipe(
            tap((data) => {
                this.data$.next(data);
            }),
            mapTo('loaded' as const),
            catchError(err => {
                console.warn('Failed loading country data', err);
                return ['error'] as const;
            }),
            startWith('loading' as const),
            tap(state => this.state$.next(state)),
            mapTo(undefined)
        );
    }
}
