import {Component, OnInit} from '@angular/core';
import {socialLinks} from "../../../util/social-links";
import {TranslateService} from "@ngx-translate/core";
import {Observable} from "rxjs";
import {map} from "rxjs/operators";

@Component({
    selector: 'app-footer',
    templateUrl: './footer.component.html',
    styleUrls: ['./footer.component.scss']
})
export class FooterComponent implements OnInit {

    socialLinks = socialLinks;

    constructor(
        private readonly translateService: TranslateService
    ) {
    }

    ngOnInit(): void {
    }

    contactFirstWord(): Observable<string> {
        return this.translateService.get('LEGAL_CONNECT').pipe(
            map((result: string) => {
                const idx = result.indexOf(' ');
                return idx < 0 ? result : result.substring(0, idx);
            })
        );
    }

    contactRest(): Observable<string> {
        return this.translateService.get('LEGAL_CONNECT').pipe(
            map((result: string) => {
                const idx = result.indexOf(' ');
                return idx < 0 ? '' : result.substring(idx);
            })
        );
    }

}
