import {AfterViewInit, Component, ElementRef, Input, OnChanges, OnDestroy, OnInit, QueryList, SimpleChanges, ViewChildren} from '@angular/core';
import {MovingRiskData, ResultOutput} from "../../../util/data";
import {combineLatest, fromEvent, Observable, Subject} from "rxjs";
import {distinctUntilChanged, filter, map, shareReplay, startWith, switchMap, takeUntil} from "rxjs/operators";
import type {Chart} from "chart.js";
import {LanguageService} from "../../../services/language.service";
import {DataService} from "../../../services/data.service";
import {ChartJsService} from "../../../services/chart-js.service";
import {Level, Species} from "../../../../generated/api";
import {TranslateService} from "@ngx-translate/core";

@Component({
    selector: 'app-moving-risk-display',
    templateUrl: './moving-risk-display.component.html',
    styleUrls: ['./moving-risk-display.component.scss']
})
export class MovingRiskDisplayComponent implements OnInit, OnChanges, AfterViewInit, OnDestroy {

    @Input()
    movingRiskData: ReadonlyArray<MovingRiskData>;

    @Input()
    species: Species;

    @Input()
    reportMode: boolean = false;

    @ViewChildren('movingCanvasElement')
    movingCanvasElements: QueryList<ElementRef<HTMLCanvasElement>>;

    haveMovingCharts = false;
    movingChartsLoaded = false;
    movingRiskHeaderText: string | null = null;
    reportHtml: string | null = null;


    private chartJsAndMovingRiskData$: Observable<[typeof Chart, Chart.ChartConfiguration] | null>;
    private readonly movingRiskData$ = new Subject<ReadonlyArray<MovingRiskData>>();
    private readonly species$ = new Subject<Species>();
    private readonly destroyed$ = new Subject();

    constructor(
        private readonly languageService: LanguageService,
        private readonly translateService: TranslateService,
        private readonly dataService: DataService,
        private readonly chartJsService: ChartJsService,
    ) {
    }

    ngOnInit(): void {
        this.species$.pipe(
            switchMap(species => {
                if (species == null) {
                    return [null];
                } else {
                    return this.translateService.get('MOVING_RISKS', {species: species.text});
                }
            }),
            takeUntil(this.destroyed$)
        ).subscribe(text => this.movingRiskHeaderText = text);

        const maxLevel$ = this.dataService.get('levels').pipe(
            map(levels => Math.max(...levels.map(l => l.level_factor ?? 0)))
        );

        const movingChartData = combineLatest([this.languageService.currentLanguage, this.movingRiskData$, maxLevel$]).pipe(
            map<[string, MovingRiskData[], number], Chart.ChartConfiguration | null>(([currentLanguage, movingRiskData, maxLevel]) => {
                console.log("in compo mrd", movingRiskData);
                if ((movingRiskData?.length ?? 0) === 0) {
                    return null;
                } else {
                    const rawLevels = movingRiskData.map(data => data.rawLevel);
                    return {
                        type: 'bar',
                        data: {
                            labels: movingRiskData.map(data => {
                              const dateString = data.date.toLocaleDateString(currentLanguage, {month: 'long'});
                              let countString = data.count.toLocaleString(currentLanguage, {
                                maximumFractionDigits: 0
                              });
                              return `${dateString}, ${data.level.text} (${countString})`
                            }),
                            datasets: [{
                                data: rawLevels,
                                backgroundColor: movingRiskData.map(data => data.level.color ?? 'black'),
                                borderColor: movingRiskData.map(data => data.level.color ?? 'black'),
                                borderWidth: 1,
                                maxBarThickness: 60
                            }],
                        },
                        options: {
                            legend: {
                                display: false
                            },
                            scales: {
                                yAxes: [{
                                    display: false,
                                    ticks: {
                                        min: 0,
                                        max: maxLevel
                                    }
                                }],
                            },

                        }
                    };
                }
            })
        );

        this.chartJsAndMovingRiskData$ = movingChartData.pipe(
            switchMap(data => {
                return data == null ? [null] : this.chartJsService.chartJs.pipe(map(chartJs => ([chartJs, data])))
            }),
            shareReplay(1),
            takeUntil(this.destroyed$)
        );

        this.chartJsAndMovingRiskData$.subscribe(chartJsAndData => {
            this.movingChartsLoaded = chartJsAndData != null;
        });

        movingChartData.subscribe(chartJsData => {
            this.haveMovingCharts = chartJsData != null;
        });

        combineLatest([this.movingRiskData$, this.languageService.currentLanguage]).pipe(
            switchMap(([data, currentLanguage]) => {
                if (data == null || data.length === 0) {
                    return null;
                }
                const cmpLevels = (ref: Level, other: Level): number => {
                    if (ref.level_factor < other.level_factor) {
                        return -1;
                    } else if (ref.level_factor > other.level_factor) {
                        return 1;
                    } else {
                        return 0;
                    }
                };

                const currentMonthLevel = data[data.length - 1].level;
                let text;
                if (data.length > 1) {
                    const prevMonthLevel = data[data.length - 2].level;
                    const cmp = cmpLevels(currentMonthLevel, prevMonthLevel);
                    let monthsCount = 1;
                    for (let idx = data.length - 3; idx >= 0; idx--) {
                        const earlyMonthLevel = data[idx].level;
                        const earlyCmp = cmpLevels(currentMonthLevel, earlyMonthLevel);
                        if (earlyCmp !== cmp) {
                            break;
                        }
                        monthsCount++;
                    }
                    const compareText = cmp === -1 ? 'MOVING_RISK_COMPARE_HIGHER' : cmp === 1 ? 'MOVING_RISK_COMPARE_LOWER' : 'MOVING_RISK_COMPARE_SAME';
                    const monthsText = monthsCount === 1 ? 'MOVING_RISK_MONTHS_ONE' : 'MOVING_RISK_MONTHS_MANY';

                    const adjustText = cmp === -1 ? 'MOVING_RISK_ADJUST_YES' : 'MOVING_RISK_ADJUST_NO';

                    return combineLatest([
                        this.translateService.get(monthsText, {months: monthsCount.toLocaleString(currentLanguage)}),
                        this.translateService.get(adjustText),
                    ]).pipe(
                        switchMap(([monthsTranslation, adjustTranslation]) => {
                            return this.translateService.get(compareText, {
                                months: monthsTranslation,
                                currentLevel: currentMonthLevel.text,
                                adjustText: adjustTranslation
                            })
                        })
                    );
                } else {
                    return this.translateService.get('MOVING_RISK_COMPARE_NONE', {
                        currentLevel: currentMonthLevel.text
                    });
                }
            }),
            takeUntil(this.destroyed$)
        ).subscribe(reportHtml => {
            this.reportHtml = reportHtml;
        })


        this.movingRiskData$.next(this.movingRiskData);
        this.species$.next(this.species);
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.movingRiskData && !changes.movingRiskData.firstChange) {
            this.movingRiskData$.next(this.movingRiskData);
        }
        if (changes.species && !changes.species.firstChange) {
            this.species$.next(this.species);
        }
    }

    ngAfterViewInit() {
        const latestMovingCanvas = this.movingCanvasElements.changes.pipe(
            startWith(0),
            filter(() => this.movingCanvasElements.length === 1),
            map(() => this.movingCanvasElements.toArray()[0].nativeElement),
            distinctUntilChanged(),
        );

        combineLatest([latestMovingCanvas, this.chartJsAndMovingRiskData$]).pipe(
            takeUntil(this.destroyed$)
        ).subscribe(([movingCanvas, chartJsAndMovingRiskData]) => {
            if (chartJsAndMovingRiskData != null) {
                const chartJs = chartJsAndMovingRiskData[0];
                new chartJs.Chart(movingCanvas, chartJsAndMovingRiskData[1]);
            }
        });
    }

    ngOnDestroy() {
        this.movingRiskData$.complete();
        this.species$.complete();
        this.destroyed$.next();
        this.destroyed$.complete();
    }

}
