import {Injectable} from '@angular/core';
import {Observable, ReplaySubject} from "rxjs";

@Injectable({
    providedIn: 'root'
})
export class ValidAccessTokenHolderService {

    private readonly currentValidAccessToken$ = new ReplaySubject<string | null>(1);

    constructor() {
    }

    get currentValidAccessToken(): Observable<string | null> {
        return this.currentValidAccessToken$;
    }

    setAccessToken(token: string | null) {
        this.currentValidAccessToken$.next(token);
    }

}
