import {Component, Input, OnChanges, OnDestroy, OnInit, SimpleChanges} from '@angular/core';
import {EMPTY, Subject, timer} from "rxjs";
import {debounce} from "rxjs/operators";

@Component({
    selector: 'app-button-spinner',
    templateUrl: './button-spinner.component.html',
    styleUrls: ['./button-spinner.component.scss'],
})
export class ButtonSpinnerComponent implements OnInit, OnChanges, OnDestroy {

    private readonly spinning$ = new Subject<boolean>();
    debouncedSpinning: boolean = false;

    @Input()
    spinning = true;

    constructor() {
    }

    ngOnInit(): void {
        this.spinning$.pipe(
            debounce(spinning => !spinning ? EMPTY : timer(500))
        ).subscribe(debouncedSpinning => this.debouncedSpinning = debouncedSpinning);
        this.debouncedSpinning = this.spinning;
        this.spinning$.next(this.spinning);
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.spinning && !changes.spinning.firstChange) {
            this.spinning$.next(this.spinning);
        }
    }

    ngOnDestroy() {
        this.spinning$.complete();
    }

}
