import { Component, OnInit } from '@angular/core';
import {Observable, of, Subject} from "rxjs";
import {FormControl, FormGroup, FormGroupDirective, NgForm, Validators} from "@angular/forms";
import {ErrorStateMatcher} from "@angular/material/core";
import {ActivatedRoute} from "@angular/router";
import {ApiService, UserService} from "../../../../generated/api";
import {TranslateService} from "@ngx-translate/core";
import {delay, first, takeUntil} from "rxjs/operators";
import {HttpErrorResponse} from "@angular/common/http";

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.scss']
})
export class ChangePasswordComponent implements OnInit {

  private readonly destroyed$ = new Subject();

  controls = {
    oldPassword: new FormControl(null, Validators.required),
    newPassword: new FormControl(null, Validators.required),
    newPasswordRepeat: new FormControl(null, Validators.required),
  } as const;
  formGroup = new FormGroup(this.controls, group => {
    const newPassword = String(this.controls.newPassword.value ?? '');
    const newPasswordRepeat = String(this.controls.newPasswordRepeat.value ?? '');
    if (newPassword.trim() === '' || newPasswordRepeat.trim() === '') {
      return null;
    }
    return newPassword === newPasswordRepeat ? null : {noMatch: true};
  });

  passwordErrorMatcher: ErrorStateMatcher;
  oldPasswordErrorMatcher: ErrorStateMatcher;
  submitted: boolean = false;
  oldPasswordError: Observable<string> | null = null;
  error: Observable<string> | null = null;
  loading: boolean = false;
  valid: boolean = true;
  token: string | null = null;

  constructor(
    private readonly route: ActivatedRoute,
    private readonly apiService: ApiService,
    private readonly userService: UserService,
    private readonly translateService: TranslateService,
  ) {
    const component = this;
    this.passwordErrorMatcher = new class extends ErrorStateMatcher {
      isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
        return super.isErrorState(control, form) || component.error != null || form.hasError('noMatch');
      }
    }
    this.oldPasswordErrorMatcher = new class extends ErrorStateMatcher {
      isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
        return super.isErrorState(control, form) || component.oldPasswordError != null;
      }
    }
  }

  ngOnInit(): void {
  }

  submit() {
    if (this.formGroup.valid) {
      this.loading = true;
      this.error = null;
      this.oldPasswordError = null;
      this.userService.userChangePasswordUpdate({
        old_password: this.controls.oldPassword.value,
        new_password: this.controls.newPassword.value
      }).pipe(
        takeUntil(this.destroyed$),
      ).subscribe({
        next: () => {
          this.loading = false;
          this.submitted = true;
        },
        error: err => {
          this.loading = false;
          console.error('Change Password failed', err);
          if (err instanceof HttpErrorResponse && Math.floor(err.status / 100) === 4 && typeof err.error === 'object') {
            if (Array.isArray(err.error.password) && err.error.password.length !== 0) {
              this.error = of(err.error.password.map(p => String(p)).join(', '));
            } else if (Array.isArray(err.error.old_password) && err.error.old_password.length !== 0) {
              this.oldPasswordError = of(err.error.old_password.map(p => String(p)).join(', '));
            }
          }
          if (this.error == null && this.oldPasswordError == null) {
            this.error = this.translateService.get('CHANGEPASSWORD_ERROR');
          }
        }
      });
    }
  }

  ngOnDestroy() {
    this.destroyed$.next();
    this.destroyed$.complete();
  }

}
