import {Injectable} from '@angular/core';
import {distinctUntilChanged, map, publishReplay, single, startWith, switchMap, tap} from "rxjs/operators";
import {combineLatest, ConnectableObservable, fromEvent, Observable} from "rxjs";
import {LanguagesService} from "../../generated/api";
import {CurrentLanguageService} from "./current-language.service";
import {TranslateService} from "@ngx-translate/core";

export interface KnownLanguage {
    readonly id: string;
    readonly englishName: string;
    readonly localName: string;
}

@Injectable({
    providedIn: 'root'
})
export class LanguageService {

    readonly knownLanguages: Observable<ReadonlyArray<KnownLanguage>>;
    readonly currentLanguage: Observable<string>;

    constructor(
        private readonly languagesService: LanguagesService,
        private readonly currentLanguageService: CurrentLanguageService,
        private readonly translateService: TranslateService
    ) {
        const languages = this.languagesService.languagesRetrieve().pipe(
            single(),
            map(apiLang => LanguageService.parseApiLanguages(apiLang)),
            publishReplay(1)
        ) as ConnectableObservable<ReadonlyArray<KnownLanguage>>;
        languages.connect();
        this.knownLanguages = languages;

        this.currentLanguage = this.currentLanguageService.currentLanguage.pipe(
            switchMap(currentLanguage => {
                if (currentLanguage == null) {
                    const languageChange = fromEvent(window, 'languagechange').pipe(startWith(0));
                    return combineLatest([this.knownLanguages, languageChange]).pipe(
                        map(([knownLanguages]) => {
                            const browserLanguages = LanguageService.getBrowserLanguagePreferences();
                            for (const bl of browserLanguages) {
                                const foundLanguage = knownLanguages.find(kl => LanguageService.languageMatch(bl, kl.id, true));
                                if (foundLanguage != null) {
                                    return foundLanguage.id;
                                }
                            }
                            for (const bl of browserLanguages) {
                                const foundLanguage = knownLanguages.find(kl => LanguageService.languageMatch(bl, kl.id, false));
                                if (foundLanguage != null) {
                                    return foundLanguage.id;
                                }
                            }
                            return knownLanguages[0].id;
                        })
                    );
                } else {
                    return [currentLanguage];
                }
            }),
            distinctUntilChanged()
        );
        this.currentLanguage.subscribe(currentLanguage => {
            this.translateService.use(currentLanguage)
        });
    }

    public setLanguage(language: string) {
        this.currentLanguageService.setLanguage(language);
    }

    private static languageMatch(browserLang: string, apiLang: string, strict: boolean): boolean {
        if (strict) {
            return browserLang.toLowerCase() === apiLang.toLowerCase();
        }
        const browserIdx = browserLang.indexOf('-');
        const apiIdx = apiLang.indexOf('-');
        if (browserIdx >= 0 && apiIdx >= 0) {
            return browserLang.toLowerCase() === apiLang.toLowerCase();
        } else {
            const browserSub = browserIdx >= 0 ? browserLang.substring(0, browserIdx) : browserLang;
            const apiSub = apiIdx >= 0 ? apiLang.substring(0, apiIdx) : apiLang;
            return browserSub.toLowerCase() === apiSub.toLowerCase();
        }
    }

    private static getBrowserLanguagePreferences(): ReadonlyArray<string> {
        if (window.navigator.languages) {
            return window.navigator.languages;
        } else if (window.navigator.language) {
            return [window.navigator.language];
        } else {
            return [];
        }
    }

    private static parseApiLanguages(apiLang: unknown): ReadonlyArray<KnownLanguage> {
        if (!Array.isArray(apiLang)) {
            throw new Error('Cannot parse API languages');
        }
        return apiLang.map(el => {
            if (!Array.isArray(el) || el.length !== 3 || el.some(l => typeof l !== 'string')) {
                throw new Error('Cannot parse API languages');
            }
            return {
                id: el[0],
                englishName: el[1],
                localName: el[2]
            }
        });
    }

}
