import {Component, OnDestroy, OnInit} from '@angular/core';
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {Customer, CustomersService, Species} from "../../../../generated/api";
import {ObservableInput, Subject} from "rxjs";
import {catchError, debounceTime, distinctUntilChanged, filter, map, startWith, switchMap, takeUntil} from "rxjs/operators";
import {LoginService} from "../../../services/login.service";
import {MatDialogRef} from "@angular/material/dialog";
import {DataService} from "../../../services/data.service";

function isCustomer(value: unknown): value is Customer {
    return value != null && typeof value === 'object' && typeof value['id'] === 'number' && typeof value['name'] === 'string';
}

@Component({
    selector: 'app-moving-risk-popup',
    templateUrl: './moving-risk-popup.component.html',
    styleUrls: ['./moving-risk-popup.component.scss']
})
export class MovingRiskPopupComponent implements OnInit, OnDestroy {

    private readonly destroyed$ = new Subject();

    readonly controls = {
        customer: new FormControl(null, [Validators.required, control => {
            if (!Validators.required(control) && !isCustomer(control.value)) {
                return {'notCustomer': true}
            }
        }]),
        speciesId: new FormControl(null, Validators.required),
        months: new FormControl(6, [Validators.required, control => {
            if (Validators.required(control)) {
                return;
            }
            const float = parseFloat(control.value);
            const int = parseInt(control.value);
            if (float != int || int <= 0 || int > 12) {
                return {'monthsInvalid': true}
            }
        }])
    } as const;
    readonly formGroup = new FormGroup(this.controls);

    sending = false;
    species: Species[] = [];
    searchResults: Customer[] = [];

    constructor(
        private readonly dialogRef: MatDialogRef<MovingRiskPopupComponent>,
        private readonly customerService: CustomersService,
        private readonly loginService: LoginService,
        private readonly dataService: DataService,
    ) {
    }

    ngOnInit(): void {
        this.loginService.currentUser.pipe(
            filter(user => !(user?.customer_database_access)),
            takeUntil(this.destroyed$)
        ).subscribe(() => this.dialogRef.close());

        this.loginService.currentUser.pipe(
            map(user => user?.customer_database_access ?? false),
            distinctUntilChanged(),
            switchMap<boolean, ObservableInput<Customer[]>>(customerDbAccess => {
                if (!customerDbAccess) {
                    return [[]];
                } else {
                    return this.controls.customer.valueChanges.pipe(
                        startWith(0),
                        map(() => this.controls.customer.value ?? ''),
                        map(searchObj => typeof searchObj === 'string' ? searchObj : (searchObj?.name ?? '')),
                        map(searchTerm => searchTerm.trim()),
                        distinctUntilChanged(),
                        debounceTime(500),
                        switchMap(searchTerm => {
                            return this.customerService.customersList(1, 10, searchTerm === '' ? undefined : searchTerm).pipe(
                                map(response => response.results ?? [])
                            )
                        }),
                        catchError(err => {
                            console.error('Failed getting autocomplete', err);
                            return [[]];
                        })
                    );
                }
            }),
            takeUntil(this.destroyed$)
        ).subscribe(searchResults => {
            this.searchResults = searchResults;
        });

        this.dataService.get('species').pipe(
            takeUntil(this.destroyed$)
        ).subscribe(species => {
            this.species = species;
        })

    }

    ngOnDestroy() {
        this.destroyed$.next();
        this.destroyed$.complete();
    }

    submit() {
        if (this.formGroup.valid && !this.sending) {
            this.sending = true;
            this.customerService.customersMovingRiskSendReportCreate(
                this.controls.customer.value.id,
                Number(this.controls.months.value),
                this.controls.speciesId.value
            ).subscribe({
                next: () => {
                    this.sending = false;
                    this.dialogRef.close();
                },
                error: err => {
                    console.error('Failed sending report', err);
                    this.sending = false;
                }
            })
        }
    }

    customerDisplayFn(customer: Customer): string {
        return customer?.name ?? ''
    }

}
