import {Injectable} from '@angular/core';
import {
    HttpRequest,
    HttpHandler,
    HttpEvent,
    HttpInterceptor
} from '@angular/common/http';
import {Observable} from 'rxjs';
import {ValidAccessTokenHolderService} from "../services/valid-access-token-holder.service";
import {environment} from "../../environments/environment";
import {first, switchMap} from "rxjs/operators";

@Injectable()
export class AuthHeaderInterceptor implements HttpInterceptor {

    constructor(
        private readonly validAccessTokenHolderService: ValidAccessTokenHolderService
    ) {
    }

    intercept(request: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<unknown>> {
        if (!AuthHeaderInterceptor.needsAuthHeader(request)) {
            return next.handle(request);
        } else {
            // console.log("waiting for access token for ", request.url);
            return this.validAccessTokenHolderService.currentValidAccessToken.pipe(
                first(),
                switchMap(token => {
                    // console.log("got access token for ", request.url)
                    const newRequest = token == null ? request : request.clone({
                        setHeaders: {
                            'Authorization': `Bearer ${token}`
                        }
                    });
                    return next.handle(newRequest);
                })
            );
        }
    }

    private static needsAuthHeader(request: HttpRequest<unknown>): boolean {
        return request.url.startsWith(environment.api) && !request.url.substr(environment.api.length).startsWith('/backend/masterdata/api/');
    }

}
