import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';

import {AppRoutingModule} from './app-routing.module';
import {AppComponent} from './app.component';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {ApiModule, Configuration} from "../generated/api";
import {environment} from "../environments/environment";
import {HTTP_INTERCEPTORS, HttpClient, HttpClientModule} from "@angular/common/http";
import {LanguageHeaderInterceptor} from "./interceptors/language-header.interceptor";
import {ComponentsModule} from "./modules/components/components.module";
import {AuthHeaderInterceptor} from "./interceptors/auth-header.interceptor";
import {TranslateLoader, TranslateModule} from "@ngx-translate/core";
import {Observable} from "rxjs";
import {ReactiveFormsModule} from "@angular/forms";
import {MatToolbarModule} from "@angular/material/toolbar";
import {MatIconModule} from "@angular/material/icon";
import {MatInputModule} from "@angular/material/input";
import {MatButtonModule} from "@angular/material/button";
import {MatDialogModule} from "@angular/material/dialog";
import {MatSelectModule} from "@angular/material/select";
import {MatFormFieldModule} from "@angular/material/form-field";
import {MatCardModule} from "@angular/material/card";
import {MatProgressSpinnerModule} from "@angular/material/progress-spinner";
import {MatMenuModule} from "@angular/material/menu";
import {RegistrationModule} from "./modules/pages/registration/registration.module";
import {ImprintModule} from "./modules/pages/imprint/imprint.module";
import {LostPasswordModule} from "./modules/pages/lost-password/lost-password.module";
import {ResetPasswordModule} from "./modules/pages/reset-password/reset-password.module";
import {MatPaginatorIntl} from "@angular/material/paginator";
import {TranslatedPaginatorI18n} from "./util/pagination-i18n";
import {MatNativeDateModule} from "@angular/material/core";
import {ChangePasswordModule} from "./modules/pages/change-password/change-password.module";

export function apiModuleConfigurationFactory(): Configuration {
    return new Configuration({
        basePath: environment.api,
        credentials: {
            'Bearer': () => undefined // dummy entry, we add this token manually using an interceptor, because we need observable support
        }
    })
}

export function httpLoaderFactory(http: HttpClient): TranslateLoader {
    return new class extends TranslateLoader {
        getTranslation(lang: string): Observable<any> {
            return http.get(`${environment.api}/backend/masterdata/translations/`, {
                headers: {
                    'Accept-Language': lang
                }
            })
        }
    }
}

@NgModule({
    declarations: [
        AppComponent
    ],
    imports: [
        BrowserModule.withServerTransition({appId: 'serverApp'}),
        HttpClientModule,
        BrowserAnimationsModule,

        ReactiveFormsModule,

        MatToolbarModule,
        MatIconModule,
        MatInputModule,
        MatButtonModule,
        MatDialogModule,
        MatSelectModule,
        MatFormFieldModule,
        MatCardModule,
        MatProgressSpinnerModule,
        MatMenuModule,
        MatNativeDateModule,

        ApiModule,
        TranslateModule.forRoot({
            loader: {
                provide: TranslateLoader,
                useFactory: httpLoaderFactory,
                deps: [HttpClient]
            }
        }),

        AppRoutingModule,
        ComponentsModule,
        RegistrationModule,
        ImprintModule,
        LostPasswordModule,
        ResetPasswordModule,
        ChangePasswordModule
    ],
    providers: [
        {
            provide: HTTP_INTERCEPTORS,
            useClass: LanguageHeaderInterceptor,
            multi: true
        },
        {
            provide: HTTP_INTERCEPTORS,
            useClass: AuthHeaderInterceptor,
            multi: true
        },
        {
            provide: MatPaginatorIntl,
            useClass: TranslatedPaginatorI18n
        },
        {
            provide: Configuration,
            useFactory: apiModuleConfigurationFactory,
        }
    ],
    bootstrap: [AppComponent]
})
export class AppModule {
}
