import {Component, OnDestroy, OnInit} from '@angular/core';
import {FormControl, FormGroup, FormGroupDirective, NgForm, Validators} from "@angular/forms";
import {Subject} from "rxjs";
import {finalize, first, takeUntil} from "rxjs/operators";
import {MatDialog, MatDialogRef} from "@angular/material/dialog";
import {LoginService} from "../../../services/login.service";
import {ErrorStateMatcher} from "@angular/material/core";

@Component({
    selector: 'app-sign-in',
    templateUrl: './sign-in.component.html',
    styleUrls: ['./sign-in.component.scss']
})
export class SignInComponent implements OnInit, OnDestroy {

    private readonly destroyed$ = new Subject();

    controls = {
        email: new FormControl('', [Validators.required, Validators.email]),
        password: new FormControl('', [Validators.required])
    } as const;

    formGroup = new FormGroup(this.controls);
    loginError: string | null = null;
    signingIn: boolean = false;
    readonly emailErrorMatcher: ErrorStateMatcher;

    constructor(
        private readonly matDialog: MatDialog,
        private readonly dialogRef: MatDialogRef<SignInComponent>,
        private readonly loginService: LoginService,
    ) {
        const component = this;
        this.emailErrorMatcher = new class extends ErrorStateMatcher {
            isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
                return super.isErrorState(control, form) || component.loginError != null;
            }
        }
    }

    ngOnInit(): void {
    }

    close() {
        this.dialogRef.close();
    }

    signIn() {
        if (this.formGroup.valid) {
            this.signingIn = true;
            this.loginError = null;
            this.loginService.login(
                this.controls.email.value, this.controls.password.value
            ).pipe(
                first(),
                finalize(() => this.signingIn = false),
                takeUntil(this.destroyed$)
            ).subscribe(loginResult => {
                if (loginResult == null) {
                    this.dialogRef.close();
                } else {
                    this.loginError = loginResult;
                }
            });
        }
    }

    ngOnDestroy() {
        this.destroyed$.next();
        this.destroyed$.complete();
    }

}
