import {MatPaginatorIntl} from "@angular/material/paginator";
import {TranslateParser, TranslateService} from "@ngx-translate/core";
import {Subject} from "rxjs";
import {Injectable} from "@angular/core";

@Injectable()
export class TranslatedPaginatorI18n extends MatPaginatorIntl {

    private rangeLabelIntl: string;

    readonly changes: Subject<void>;
    itemsPerPageLabel: string;
    nextPageLabel: string;
    previousPageLabel: string;
    firstPageLabel: string;
    lastPageLabel: string;

    constructor(private readonly translateService: TranslateService, private readonly translateParser: TranslateParser) {
        super();
        this.translateService.get([
            'PAGINATOR_ITEMS_PER_PAGE',
            'PAGINATOR_NEXT_PAGE',
            'PAGINATOR_PREVIOUS_PAGE',
            'PAGINATOR_FIRST_PAGE',
            'PAGINATOR_LAST_PAGE',
            'PAGINATOR_RANGE'
        ]).subscribe(translation => {
                this.itemsPerPageLabel = translation['PAGINATOR_ITEMS_PER_PAGE'];
                this.nextPageLabel = translation['PAGINATOR_NEXT_PAGE'];
                this.previousPageLabel = translation['PAGINATOR_PREVIOUS_PAGE'];
                this.firstPageLabel = translation['PAGINATOR_FIRST_PAGE'];
                this.lastPageLabel = translation['PAGINATOR_LAST_PAGE'];
                this.rangeLabelIntl = translation['PAGINATOR_RANGE'];
                this.changes.next();
            });
    }

    getRangeLabel = (page: number, pageSize: number, length: number): string => {
        length = Math.max(length, 0);
        const startIndex = page * pageSize;
        const endIndex = startIndex < length ? Math.min(startIndex + pageSize, length) : startIndex + pageSize;
        return this.translateParser.interpolate(this.rangeLabelIntl, { startIndex, endIndex, length });
    }
}