import {Component, OnDestroy, OnInit} from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {Observable, of, Subject} from "rxjs";
import {first, takeUntil} from "rxjs/operators";
import {ApiService} from "../../../../generated/api";
import {FormControl, FormGroup, FormGroupDirective, NgForm, Validators} from "@angular/forms";
import {HttpErrorResponse} from "@angular/common/http";
import {ErrorStateMatcher} from "@angular/material/core";
import {TranslateService} from "@ngx-translate/core";

@Component({
    selector: 'app-reset-password',
    templateUrl: './reset-password.component.html',
    styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit, OnDestroy {

    private readonly destroyed$ = new Subject();

    controls = {
        token: new FormControl(null, Validators.required),
        newPassword: new FormControl(null, Validators.required)
    } as const;
    formGroup = new FormGroup(this.controls);

    passwordErrorMatcher: ErrorStateMatcher;
    submitted: boolean = false;
    error: Observable<string> | null = null;
    loading: boolean = false;
    valid: boolean = true;
    token: string | null = null;

    constructor(
        private readonly route: ActivatedRoute,
        private readonly apiService: ApiService,
        private readonly translateService: TranslateService,
    ) {
        const component = this;
        this.passwordErrorMatcher = new class extends ErrorStateMatcher {
            isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
                return super.isErrorState(control, form) || component.error != null;
            }
        }
    }

    ngOnInit(): void {
        this.route.params.pipe(
            first(),
            takeUntil(this.destroyed$)
        ).subscribe(routeParams => {
            this.token = routeParams['token'];
            if (this.token) {
                this.controls.token.setValue(this.token);
                this.controls.token.disable();
            } else {
                this.controls.token.enable();
            }
        })
    }

    get hasToken(): boolean {
        return this.token != null;
    }

    submit() {
        if (this.formGroup.valid) {
            this.loading = true;
            this.error = null;
            this.apiService.apiPasswordResetConfirmCreate({
                password: this.controls.newPassword.value,
                token: this.controls.token.value
            }).pipe(
                takeUntil(this.destroyed$),
            ).subscribe({
                next: () => {
                    this.loading = false;
                    this.submitted = true;
                },
                error: err => {
                    this.loading = false;
                    console.error('Reset Password failed', err);
                    if (err instanceof HttpErrorResponse && Math.floor(err.status / 100) === 4 && typeof err.error === 'object' && Array.isArray(err.error.password)) {
                        this.error = of(err.error.password.map(p => String(p)).join(', '));
                    } else {
                        this.error = this.translateService.get('RESETPASSWORD_FAILED');
                    }
                }
            });
        }
    }

    ngOnDestroy() {
        this.destroyed$.next();
        this.destroyed$.complete();
    }

}
