import {Component, Inject} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from "@angular/material/dialog";
import {ThemePalette} from "@angular/material/core";

export interface AlertButton {
    readonly id: string;
    readonly text: string;
    readonly role?: 'primary' | 'default';
}

export interface AlertData {
    readonly title: string;
    readonly message: string;
    readonly icon?: string;
    readonly iconColor?: ThemePalette;
    readonly buttons?: ReadonlyArray<AlertButton>;
}

export interface AlertResult {
    readonly button?: string;
}

@Component({
    selector: 'app-alert',
    templateUrl: './alert.component.html',
    styleUrls: ['./alert.component.scss']
})
export class AlertComponent {

    constructor(
        @Inject(MAT_DIALOG_DATA) readonly data: AlertData,
        private readonly dialogRef: MatDialogRef<AlertComponent, AlertResult>
    ) {
    }

    btn(id: string) {
        this.dialogRef.close({
            button: id
        });
    }
}
