import {Injectable} from '@angular/core';
import {LocalStorageHandle, LocalStorageService} from "./local-storage.service";
import {Observable} from "rxjs";

@Injectable({
    providedIn: 'root'
})
export class CurrentLanguageService {

    private readonly languageHandle: LocalStorageHandle<string>;

    constructor(
        private readonly localStorageService: LocalStorageService,
    ) {
        this.languageHandle = this.localStorageService.newHandle('risk.language', raw => raw, language => language);
        this.languageHandle.subscribe();
    }

    get currentLanguage(): Observable<string | null> {
        return this.languageHandle.value;
    }

    setLanguage(language: string) {
        this.languageHandle.setValue(language);
    }
}
